<template>
  <div class="bp full-screen">
    <img class="bp-logo" :src="project.logo" />
  </div>
</template>

<script type="text/ecmascript-6">
import BP from './BP.jpg'

export default {
  components: {
    BP
  },
  data () {
    return {
      project: {
        logo: BP
      }
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .bp-logo
    max-width: 7.5rem
    width: 100%
</style>



